.benefits {
  width: 100%;
  padding-top: 100px;
  position: relative;

  .benefits__container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 1284px;
    // height: 1093px;
    background: #1d2532;
    border-radius: 20px;
    margin: 0 auto;
    // padding: 115px;
    margin-bottom: 100px;

    .benefits__image27 {
      position: absolute;
      width: 400px;
      height: 400px;
      top: 690px;
      left: 2px;
    }
    .benefits__image223 {
      position: absolute;
      width: 400px;
      height: 400px;
      left: 885px;
    }
    .benefits__image126 {
      position: absolute;
      top: 90px;
      left: 200px;
    }
    .benefits__image266 {
      margin-top: 0px;
      position: absolute;
      top: -90px;
      left: 1000px;
    }
    .benefits__image125 {
      position: absolute;
      top: 800px;
      left: 1000px;
    }

    .benefits__title {
      width: 596px;
      font-weight: 600;
      font-size: 45px;
      line-height: 58.5px;
      color: #ffffff;
      text-align: center;
      margin: 0 auto;
      margin-bottom: 20px;
      z-index: 60;
      padding-top: 100px;
    }
    .benefits__cards {
      width: 650px;
      height: 710px;
      margin-top: 50px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;
      padding-bottom: 155px;

      .cards__wrapper {
        position: relative;
        width: 300px;
        height: 310px;
        border-radius: 20px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0;
        z-index: 200;

        .benefits__card {
          position: relative;
          width: 300px;
          height: 310px;
          border-radius: 20px;
          background-color: #333e51;
          margin-bottom: 20px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin: 0;
          z-index: 200;

          .card__img {
            max-width: 100%;
            max-height: 100%;
            object-fit: contain;
          }

          .benefits__text1 {
            width: 236px;
            height: 75px;
            font-weight: 400;
            font-size: 19px;
            line-height: 25px;
            letter-spacing: -0.7;
            align-items: center;
            color: #ffffff;
            margin: 0 auto;
            text-align: center;
          }
          .benefits__text2 {
            width: 236px;
            height: 50px;
            font-weight: 400;
            font-size: 19px;
            line-height: 25px;
            letter-spacing: -0.7;
            align-items: center;
            color: #ffffff;
            margin: 0 auto;
            text-align: center;
          }
          .benefits__text3 {
            width: 260px;
            height: 75px;
            font-weight: 400;
            font-size: 19px;
            line-height: 25px;
            letter-spacing: -0.7;
            align-items: center;
            color: #ffffff;
            margin: 0 auto;
            text-align: center;
          }
          .benefits__text4 {
            width: 236px;
            height: 51px;
            font-weight: 400;
            font-size: 19px;
            line-height: 25px;
            letter-spacing: -0.7;
            align-items: center;
            color: #ffffff;
            margin: 0 auto;
            text-align: center;
          }
        }
      }
    }
  }
}

.benefits__shadows {
  position: absolute;
  top: 60px;
  left: 20px;
  width: 260px;
  height: 268px;
  border-radius: 20px;
  background-color: rgba(51, 62, 81, 0.6);
  z-index: 1;
  // margin-bottom: 150px;
}

